import { Component, OnInit, Output, EventEmitter, Input} from '@angular/core';

@Component({
  selector: 'app-painel-perguntas',
  templateUrl: './painel-perguntas.component.html',
  styleUrls: ['./painel-perguntas.component.css']
})
export class PainelPerguntasComponent implements OnInit {

  @Output() public eventoBotao : EventEmitter<string> = new EventEmitter();
  @Input() public fraseIngles : string;
  @Input() public frasePortugues : string;

  public fraseAtual : string;

  constructor() { 
    
  }

  ngOnInit() {
  }
  conferirResposta(e){
    this.eventoBotao.emit(this.fraseAtual);
    this.fraseAtual="";
  }
}
