import { Frase } from './Frase';

export class ArrayFrases{
    private numeroAleatorio : Number;
    public frases : Frase[] = [
        new Frase('Who','Quem'),
        new Frase('Hungry','Fome'),
        new Frase('Chicken','Frango'),
        new Frase('Thirsty','Sede'),
        new Frase('Cheese ','Queijo'),
        new Frase('How','Como'),
        new Frase('Cat','gato'),
        new Frase('Alligator ','Jacare'),
        new Frase('Camel','Camelo'),
        new Frase('Dog','Cachorro'),
        new Frase('Fish','Peixe'),
        new Frase('Love','Amor'),
        new Frase('Eleven','Onze'),
        new Frase('Be happy','Seja feliz'),
        new Frase('Two','Dois'),

    ];
    constructor(){
        
    }
    public fraseAleatoria() : any{
        
       this.numeroAleatorio = (Math.floor(Math.random()*this.frases.length));
       return [ this.frases[this.numeroAleatorio.toFixed()] , this.numeroAleatorio ];
    }
    //Remove um elemento da posição sorteada
    public removerFrase(index) : void{
        this.frases.splice(index,1);
    }
}