import { Component, OnInit, Output, EventEmitter } from '@angular/core';


@Component({
  selector: 'app-painel-instrucoes',
  templateUrl: './painel-instrucoes.component.html',
  styleUrls: ['./painel-instrucoes.component.css']
})
export class PainelInstrucoesComponent implements OnInit {
  @Output() public eventoComecarJogo : EventEmitter<string> = new EventEmitter();
  constructor() { }

  ngOnInit() {
  }

  public comecarJogo(){
      this.eventoComecarJogo.emit('comecar');
  }
}
