import { Component} from '@angular/core';
import { CommonModule } from '@angular/common';

import { ArrayFrases } from '../compartilhado/ArrayFrases';

@Component({
      selector: 'app-painel-principal',
      templateUrl: './painel-principal.component.html',
      styleUrls: ['./painel-principal.component.css']
})
export class PainelPrincipalComponent{

      public fraseEN: String;
      private frases: ArrayFrases;
      private novaFraseSorteada: any;
      private frasePT: String;
      public progresso: string = '';
      private progressoController: number = 0;
      public erros: number = 0;

      //Atributos para controle de exibição
      public jogoEmAndamento: boolean = false;
      public inicioDeJogo : boolean = true;
      public fimDeJogo : boolean;
      public vitoria : boolean = false;
      public telaDerrota : boolean = false;

      constructor() {
            this.frases = new ArrayFrases();
            this.comecarRodada();
      }
    
      public comecarJogo(resposta : string) : void{
            this.jogoEmAndamento = true;
            this.inicioDeJogo = false;
      }

      public comecarRodada(): void {
            //Retorna um array com duas posicoes: A primeira um objeto frase e a segunda o indice da frase;    
            this.novaFraseSorteada = this.frases.fraseAleatoria();

            //Atribuindo valores aos atributos do objeto
            this.fraseEN = this.novaFraseSorteada[0].fraseIngles;
            this.frasePT = this.novaFraseSorteada[0].frasePortugues;
      }
      conferirResposta(resposta: string) {
            if (resposta && resposta.toLowerCase() == this.frasePT.toLowerCase()) {
                  this.frases.removerFrase(this.novaFraseSorteada[1]);
                  this.progressoController += 20;
                  this.progresso = this.progressoController + '%';
                  console.log(this.progresso);
                  this.comecarRodada();

            }
            else {
                  this.erros++;
                  if (this.erros == 4) {
                        this.vitoria=false;
                        this.erros = 0;
                        this.progressoController = 0;
                        this.progresso = '';
                        this.jogoEmAndamento = false;
                        this.fimDeJogo = true;
                        this.telaDerrota = true;
                  }
            }
            if(this.progressoController==100){
                  this.erros = 0;
                  this.progressoController = 0;
                  this.progresso = '';
                  this.jogoEmAndamento = false;
                  this.fimDeJogo = true;
                  this.vitoria=true;
            }
      }

}
